                        <div class="perempuan flex-fill ml-3">
                          <h3 class="mb-0 d-flex align-items-center justify-content-between" style="line-height:1.25; font-weight:900">73 <span class="label bg-perempuan radius-50 label-pills label-inline ml-3">30%</span></h3>
                          <span>Perempuan</span>
                        </div>
                      </div>
                      <div class="d-flex align-items-center min-w-150px mt-4">
                        <i class="genderless ri-genderless-line ri-2x"></i>
                        <div class="genderless flex-fill ml-3">
                          <h3 class="mb-0 d-flex align-items-center justify-content-between" style="line-height:1.25; font-weight:900">73 <span class="label bg-genderless radius-50 label-pills label-inline ml-3">10%</span></h3>
                          <span>Tidak diketahui</span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="card card-custom mt-4 animated">
            <div class="card-body">
              <div class="row">
                <div class="col-3">
                  <span style="font-size:14px" class="d-block apexcharts-title-text mb-5">Status Karyawan</span>
                  <div>
                    <b-progress :max="429" height="5rem" class="text-right position-relative">
                      <span class="proggress-label-custom">409 aktif</span>
                      <b-progress-bar :value="409" class="proggress-aktif" />
                    </b-progress>
                    <b-progress :max="429" height="5rem" class="text-right position-relative mt-5">
                      <span class="proggress-label-custom">20 tidak aktif</span>
                      <b-progress-bar :value="20" class="proggress-nonaktif" />
                    </b-progress>
                  </div>
                </div>
                <div class="col-2 pr-0 position-relative">
                  <apexchart :height="200" type="pie" :options="kontrakChartOpt" :series="genderChartSeries"></apexchart>
                </div>
                <div class="col-auto pl-0">
                  <table class="table-noborder">
                    <tr>
                      <td class="font-size-h4 font-weight-bolder text-right pr-3">204</td>
                      <td class="font-size-h4 font-weight-bolder text-right">Tenaga Kerja Kontrak</td>
                    </tr>
                    <tr>
                      <td class="font-size-h4 font-weight-bolder text-right pr-3">4</td>
                      <td class="font-size-h4 font-weight-bolder text-right">Tenaga Kerja Kontrak</td>
                    </tr>
                    <tr>
                      <td class="font-size-h4 font-weight-bolder text-right pr-3">20</td>
                      <td class="font-size-h4 font-weight-bolder text-right">Tenaga Kerja Kontrak</td>
                    </tr>
                  </table>
