</template>

<script>
import moment from 'moment'
moment.locale('id')
export default {
  name: 'detail-companies',
  data () {
    return {
      title: 'Perusahaan',
      id: 1,
      config: {
        permission: {
          create: 'create-companies',
          read: 'view-companies',
          update: 'update-companies',
          delete: 'delete-companies'
        }
      },
      mainYear: new Date(),
      mainData: {},
      slaveList: [
        { id: 'dashboard', name: 'Dashboard HRM', path: null, component: false },
        { id: 'struktur', name: 'Struktur Organisasi', path: 'company-organization-structures', component: false },
        { id: 'branch', name: 'Cabang Perusahaan', path: 'perusahaan/company-branches', component: true },
        { id: 'document', name: 'Dokumen Perusahaan', path: 'perusahaan/company-general-documents', component: true },
        { id: 'asset', name: 'Inventaris Aset', path: 'perusahaan/company-inventaris-assets', component: true }
      ],
      activeSlave: null,
      deptDivChartOpt: {
        colors: ['#fd743e'],
        title: {
          text: '- Departemen / Divisi'
        },
        grid: {
          show: false
        },
        chart: {
          type: 'bar',
          toolbar: {
            show: false
          }
        },
        plotOptions: {
          bar: {
            horizontal: true,
            borderRadius: 12
            // dataLabels: {
            //   position: 'middle'
            // }
          }
        },
        dataLabels: {
          textAnchor: 'start',
          // offsetX: 25,
          offsetY: -2.75,
          style: {
            colors: ['#fff'],
            fontSize: '14px',
            fontWeight: 600
          }
        },
        yaxis: {
          labels: {
            maxWidth: 350,
            align: 'left'
          }
        },
        xaxis: {
          labels: {
            show: false
          },
          axisBorder: {
            show: false
          },
          axisTicks: {
            show: false
          }
        },
        noData: {
          text: 'Loading Chart',
          align: 'center',
          verticalAlign: 'middle',
          offsetX: 0,
          offsetY: 0,
          style: {
            color: undefined,
            fontSize: '14px',
            fontFamily: undefined
          }
        }
      },
      deptDivChartSeries: [
        {
          data: [
            { x: 'Internal Audit', y: 10 },
            { x: 'Engineering', y: 15 },
            { x: 'Bussiness Development', y: 12 },
            { x: 'Marketing', y: 18 },
            { x: 'Risk Management', y: 13 },
            { x: 'Human Capital And General Affairs', y: 17 },
            { x: 'Accounting And Finance', y: 11 },
            { x: 'Corporate Secretariat', y: 16 }
          ]
        }
      ],
      usiaChartOpt: {
        colors: ['#773b94'],
        title: {
          text: '- Rentang Usia'
        },
        grid: {
          show: false
        },
        chart: {
          type: 'bar',
          toolbar: {
            show: false
          }
        },
        plotOptions: {
          bar: {
            horizontal: true,
            borderRadius: 8
            // dataLabels: {
            //   position: 'start'
            // }
          }
        },
        dataLabels: {
          textAnchor: 'start',
          // offsetX: 25,
          offsetY: -1.25,
          style: {
            colors: ['#fff'],
            fontWeight: 600
          }
        },
        yaxis: {
          labels: {
            maxWidth: 350,
            align: 'left'
          }
        },
        xaxis: {
          labels: {
            show: false
          },
          axisBorder: {
            show: false
          },
          axisTicks: {
            show: false
          }
        },
        noData: {
          text: 'Loading Chart',
          align: 'center',
          verticalAlign: 'middle',
          offsetX: 0,
          offsetY: 0,
          style: {
            color: undefined,
            fontSize: '14px',
            fontFamily: undefined
          }
        }
      },
      usiaChartSeries: [
        {
          data: [
            { x: '18 - 24', y: 100 },
            { x: '25 - 34', y: 240 },
            { x: '35 - 44', y: 126 },
            { x: '45 - 54', y: 111 },
            { x: '55 - 65', y: 50 }
          ]
        }
      ],
      genderChartOpt: {
        title: {
          text: '- Gender'
        },
        colors: ['#ec4dff', '#934dff', '#a0a7c6'],
        chart: {
          type: 'pie'
        },
        plotOptions: {
          pie: {
            expandOnClick: false,
            dataLabels: {
              offset: -15,
              minAngleToShowLabel: 10
            },
            donut: {
              labels: {
                show: false
              }
            }
          }
        },
        dataLabels: {
          enabled: true,
          textAnchor: 'middle',
          style: {
            fontSize: '50px',
            fontWeight: '900',
            colors: ['#fff']
          },
          background: {
            enabled: false
          }
        },
        legend: {
          show: false,
          position: 'right',
          horizontalAlign: 'center',
          floating: false
        },
        xaxis: {
          type: 'category'
        },
        noData: {
          text: 'Loading Chart',
          align: 'center',
          verticalAlign: 'middle',
          offsetX: 0,
          offsetY: 0,
          style: {
            color: undefined,
            fontSize: '14px',
            fontFamily: undefined
          }
        },
        labels: ['Perempuan', 'Laki - Laki', 'Tidak Diketahui'],
        stroke: {
          show: false
        }
      },
      kontrakChartOpt: {
        title: {
          text: 'Status Kontrak'
        },
        colors: ['#39409c', '#8b7edc', '#46bf93'],
        chart: {
          type: 'pie'
        },
        plotOptions: {
          pie: {
            expandOnClick: false,
            dataLabels: {
              offset: -15,
              minAngleToShowLabel: 10
            },
            donut: {
              labels: {
                show: false
              }
            }
          }
        },
        dataLabels: {
          enabled: true,
          textAnchor: 'middle',
          style: {
            fontSize: '50px',
            fontWeight: '900',
            colors: ['#fff']
          },
          background: {
            enabled: false
          }
        },
        legend: {
          show: false,
          position: 'right',
          horizontalAlign: 'center',
          floating: false
        },
        xaxis: {
          type: 'category'
        },
        noData: {
          text: 'Loading Chart',
          align: 'center',
          verticalAlign: 'middle',
          offsetX: 0,
          offsetY: 0,
          style: {
            color: undefined,
            fontSize: '14px',
            fontFamily: undefined
          }
        },
        labels: ['Tenaga Kerja Tetap', 'Tenaga Kerja Kontrak', 'Management'],
        stroke: {
          show: false
        }
      },
      karyawanChartOpt: {
        colors: ['#fd743e'],
        title: {
          text: 'Status Karyawan'
        },
        grid: {
          show: false
        },
        chart: {
          type: 'bar',
          toolbar: {
            show: false
          }
        },
        plotOptions: {
          bar: {
            horizontal: true,
            borderRadius: 12
            // dataLabels: {
            //   position: 'middle'
            // }
          }
        },
        dataLabels: {
          textAnchor: 'start',
          // offsetX: 25,
          offsetY: -2.75,
          style: {
            colors: ['#fff'],
            fontSize: '14px',
            fontWeight: 600
          }
        },
        yaxis: {
          labels: {
            show: false,
            maxWidth: 350,
            align: 'left'
          }
        },
        xaxis: {
          labels: {
            show: false
          },
          axisBorder: {
            show: false
          },
          axisTicks: {
            show: false
          }
        },
        noData: {
          text: 'Loading Chart',
          align: 'center',
          verticalAlign: 'middle',
          offsetX: 0,
          offsetY: 0,
          style: {
            color: undefined,
            fontSize: '14px',
            fontFamily: undefined
          }
        }
      },
      karyawanChartSeries: [
        {
          data: [
            { x: 'Aktif', y: 100 },
            { x: 'Tidak Aktif', y: 15 }
          ]
        }
      ],
      genderChartSeries: [73, 150, 50]
    }
  },
  computed: {
    componentLoader () {
      if (this.activeSlave.component) {
        let loader = this.activeSlave.path
        return () => import(`@/modules/${loader}.vue`)
      }
    }
  },
  beforeMount () {
    this.setActiveSlave(this.slaveList[0])
  },
  mounted () {
    this.getCompanyData()
  },
  methods: {
    getCompanyData () {
      this.$_api.single('/companies', null, this.id).then(res => {
        this.$set(this.mainData, 'company', res.data)
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    setActiveSlave (e) {
      this.activeSlave = Object.assign({}, e)
      if (e.component) this.$store.commit('set', ['activeSlave', { master: this.title }, true])
      else {
        let filter = {
          periode_year: moment(this.mainYear).set('date', 1).set('month', 0).format('YYYY-MM-DD')
        }
        if (e.path) {
          this.$_api.list(e.path, filter).then(res => {
            this.$set(this.mainData, e.id, res.data[0])
          }).catch((err) => {
            this.$_alert.error(err)
          })
        }
      }
    },
    redirect (name, view, id = null, param = {}) {
      this.$router.push({
        name: name,
        query: { ...{ view: view, id: id }, ...param }
      }).catch((err) => {
        return err
      })
    }
  }
}
</script>
<style>
  .apexcharts-title-text {
    font-weight: 800 !important;
  }
  .proggress-label-custom {
    /* display: block; */
    position: absolute;
    /* width: 10px; */
    text-align: right;
    font-size: 1.75rem;
    font-weight: 600;
    top: 50%;
    right:0;
    transform: translate(-50%, 0);
  }
  .proggress-aktif {
    background-color: #45be93;
  }
  .proggress-nonaktif {
    background-color: #e8607c;
  }
</style>
<style scoped>
  .laki {
    color: #934dff;
  }
  .perempuan {
    color: #ec4dff;
  }
  .genderless {
    color: #a0a7c6;
  }
  .bg-laki {
    color: #934dff;
    background-color: #e9dffc;
    font-weight: 800;
  }
  .bg-perempuan {
    color: #ec4dff;
    background-color: #f7dffc;
    font-weight: 800;
  }
  .bg-genderless {
    color: #a0a7c6;
    background-color: #ebecf3;
    font-weight: 800;
  }
  .radius-50 {
    border-radius: 25px;
  }
  .hero-banner {
    position: relative;
    background-image: url('/static/img/default/login.svg');
    background-color: #492c9e;
    background-repeat: no-repeat;
    background-clip: content-box;
    background-size: cover;
    /* background-position: center top; */
    background-position: center center;
    border-radius: 20px;
    min-height: 350px;
    display: flex;
    flex-direction: column;
    /* align-content: flex-end; */
    justify-content: end;
  }
  .hero-profile {
    display: flex;
    position: relative;
    padding: 2.5rem;
  }
  .img-hero-wrapper {
    padding: 1rem;
    border: 3px solid #fff;
    background-color: #fff;
    border-radius: 13px;
    align-self: flex-start;
  }
  .img-hero-wrapper img.hero-logo {
    width: 100px;
    height: 100px;
    object-fit: contain;
    object-position: center;
  }
  .hero-detail {
    display: flex;
    flex-direction: column;
    margin-left: 1.5rem;
    /* align-items: center; */
    justify-content: center;
  }
</style>



